import React, { useState } from 'react';
import data from './data';
function App() {
  const [count, setCount] = useState(0);
  const [paras, setParas] = useState([]);
  const handelSubmit = (e) => {
    e.preventDefault();
    let amount = parseInt(count);
    if(count<0){
      amount = 1;
    }
    if(count>8) {
      amount = 8;
    }
    setParas(data.slice(0, amount));
  }
  return (
      <section className='section-center'>
        <h3>TIRED OF BORING LOREM IPSUM?</h3>
        <div>
          <form onSubmit={handelSubmit}>
            <label htmlFor="paragrphs">Paragraphs:</label>
            <input type="number" id="paragraphs" name="paragraphs" value={count} onChange={(e) => {setCount(e.target.value)}}/>
            <button className='btn'>Generate</button>
          </form>
        </div>
        <div>
          {paras.map((item, index) => {
            return (
              <p key={index} className='lorem-text'>{item}</p>
            )
          })}
        </div>
      </section>
    )
}

export default App;